import React, {Component} from 'react';

class BitcoinPrice extends Component{
  constructor(props){
    super(props);
    this.state = {
      currency: 'USD'
    };
  }
  render(){
    return (
      <div>
      Prices
      </div>
    );
  }
}

export default BitcoinPrice;



